body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 80vh;
  justify-content: center;
  align-items: center;
  user-select: none;
  overflow-x: hidden;
}

.box-container {
  position: relative;
}
.row {
  display: flex;
}
.row div {
  height: 50px;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  padding: 0;
  margin: 0;
}
.row div:hover {
  cursor: pointer;
  background-color: #eee;
}
.border-right {
  border-right: 1px solid black;
}
.border-bottom {
  border-bottom: 1px solid black;
}
.border-left {
  border-left: 1px solid black;
}
.border-top {
  border-top: 1px solid black;
}

/* for right diagonal line */
.lines {
  position: absolute;
  width: 135%;
  top: 1px;
  height: 0px;
  border-top: 1px solid green;
  border-width: 4px;

  transform: rotate(-43.5deg);
  transform-origin: right;
  right: 0px;
}
/* for left diagonal line */
.lines {
  position: absolute;
  width: 135%;
  top: 1px;
  height: 1px;
  background-color: green;
  /* border-top: 1px solid green;
  border-width: 4px; */
  transform: rotate(43.5deg);
  transform-origin: left;
  left: 0px;
}
/* for first horizontal line */
.lines {
  position: absolute;
  top: 22px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: green;
  border-width: 4px;
}

/* for second horizontal line */
.lines {
  position: absolute;
  top: 73px;
  width: 100%;
  height: 1px;
  background-color: green;
  border-width: 4px;
}

/* for third horizontal line */
.lines {
  position: absolute;
  bottom: 25px;
  width: 100%;

  height: 1px;
  background-color: green;
  border-width: 4px;
}

/* for first vertical line */

.lines {
  position: absolute;
  top: 0;
  transform: rotate(90deg);
  transform-origin: left;
  width: 100%;
  height: 1px;
  background-color: green;
  border-width: 4px;
  left: 26px;
}
/* for second vertical line */
.lines {
  position: absolute;
  top: 0;
  transform: rotate(90deg);
  transform-origin: left;
  width: 100%;
  height: 1px;
  background-color: green;
  border-width: 4px;
  left: 76px;
}
/* for third vertical line */

.lines {
  position: absolute;
  top: 0;
  transform: rotate(90deg);
  transform-origin: left;
  width: 100%;
  height: 1px;
  background-color: green;
  border-width: 4px;
  left: 128px;
}
@keyframes example {
  from {
    width: 0px;
  }
  to {
    width: 100%;
  }
}
@keyframes diagonal {
  from {
    width: 0px;
  }
  to {
    width: 135%;
  }
}
